/**
 * HTTP请求工具
 * 使用宿主提供的GressBridge进行API调用
 */

/**
 * API响应接口
 */
export interface ApiResponse<T = any> {
  success: boolean
  data?: T
  message?: string
  code?: number
}

/**
 * 获取GressBridge实例
 */
function getGressBridge(): any {
  if (typeof window !== 'undefined' && (window as any).GressBridge) {
    return (window as any).GressBridge
  }
  throw new Error('GressBridge未初始化')
}

/**
 * HTTP请求方法
 */
export const http = {
  /**
   * GET请求
   */
  async get<T = any>(url: string, params?: Record<string, any>): Promise<ApiResponse<T>> {
    const bridge = getGressBridge()
    
    // 构建查询字符串
    let fullUrl = url
    if (params) {
      // 过滤掉 undefined、null 和空字符串的参数
      const filteredParams: Record<string, string> = {}
      for (const [key, value] of Object.entries(params)) {
        if (value !== undefined && value !== null && value !== '') {
          filteredParams[key] = String(value)
        }
      }
      
      if (Object.keys(filteredParams).length > 0) {
        const queryString = new URLSearchParams(filteredParams).toString()
        fullUrl = `${url}?${queryString}`
      }
    }
    
    try {
      const response = await bridge.http.get(fullUrl)
      
      // 检查业务逻辑是否成功
      if (response && response.success === false) {
        const errorMessage = response.message || '请求失败'
        throw new Error(errorMessage)
      }
      
      return response
    } catch (error) {
      // 重新抛出异常，而不是返回对象
      throw error
    }
  },

  /**
   * POST请求
   */
  async post<T = any>(url: string, data?: any): Promise<ApiResponse<T>> {
    const bridge = getGressBridge()
    
    try {
      const response = await bridge.http.post(url, data)
      
      // 检查业务逻辑是否成功
      if (response && response.success === false) {
        const errorMessage = response.message || '请求失败'
        throw new Error(errorMessage)
      }
      
      return response
    } catch (error) {
      // 重新抛出异常，而不是返回对象
      throw error
    }
  },

  /**
   * PUT请求
   */
  async put<T = any>(url: string, data?: any): Promise<ApiResponse<T>> {
    const bridge = getGressBridge()
    
    try {
      const response = await bridge.http.put(url, data)
      
      // 检查业务逻辑是否成功
      if (response && response.success === false) {
        const errorMessage = response.message || '请求失败'
        throw new Error(errorMessage)
      }
      
      return response
    } catch (error) {
      // 重新抛出异常，而不是返回对象
      throw error
    }
  },

  /**
   * DELETE请求
   */
  async delete<T = any>(url: string): Promise<ApiResponse<T>> {
    const bridge = getGressBridge()
    
    try {
      const response = await bridge.http.delete(url)
      
      // 检查业务逻辑是否成功
      if (response && response.success === false) {
        const errorMessage = response.message || '请求失败'
        throw new Error(errorMessage)
      }
      
      return response
    } catch (error) {
      // 重新抛出异常，而不是返回对象
      throw error
    }
  }
}
