/**
 * 通用的 localStorage 存储工具类
 * 
 * 提供类型安全的 localStorage 操作，支持：
 * - 自动序列化/反序列化 JSON
 * - 过期时间管理
 * - 类型安全
 * - 错误处理
 */

export interface StorageItem<T = any> {
  value: T
  timestamp?: number
  expire?: number // 过期时间（毫秒），如果设置了，timestamp + expire 为过期时间点
}

/**
 * localStorage 工具类
 */
export class Storage {
  /**
   * 设置值
   * 
   * @param key 键名
   * @param value 值（会自动序列化为 JSON）
   * @param expire 过期时间（毫秒），可选
   */
  static set<T>(key: string, value: T, expire?: number): void {
    try {
      const item: StorageItem<T> = {
        value,
        timestamp: Date.now(),
        expire
      }
      localStorage.setItem(key, JSON.stringify(item))
    } catch (error) {
      console.error(`[Storage] 设置值失败: ${key}`, error)
      throw error
    }
  }

  /**
   * 获取值
   * 
   * @param key 键名
   * @param defaultValue 默认值，如果键不存在或已过期则返回此值
   * @returns 值或默认值
   */
  static get<T>(key: string, defaultValue?: T): T | undefined {
    try {
      const itemStr = localStorage.getItem(key)
      if (!itemStr) {
        return defaultValue
      }

      const item: StorageItem<T> = JSON.parse(itemStr)
      
      // 检查是否过期
      if (item.expire && item.timestamp) {
        const now = Date.now()
        const expireTime = item.timestamp + item.expire
        if (now > expireTime) {
          // 已过期，删除并返回默认值
          localStorage.removeItem(key)
          return defaultValue
        }
      }

      return item.value
    } catch (error) {
      console.error(`[Storage] 获取值失败: ${key}`, error)
      // 解析失败时删除无效数据
      localStorage.removeItem(key)
      return defaultValue
    }
  }

  /**
   * 获取值（必须存在，否则抛出错误）
   * 
   * @param key 键名
   * @returns 值
   * @throws 如果键不存在或已过期
   */
  static getRequired<T>(key: string): T {
    const value = this.get<T>(key)
    if (value === undefined) {
      throw new Error(`Storage key "${key}" is required but not found`)
    }
    return value
  }

  /**
   * 删除值
   * 
   * @param key 键名
   */
  static remove(key: string): void {
    try {
      localStorage.removeItem(key)
    } catch (error) {
      console.error(`[Storage] 删除值失败: ${key}`, error)
    }
  }

  /**
   * 检查键是否存在且未过期
   * 
   * @param key 键名
   * @returns 是否存在
   */
  static has(key: string): boolean {
    try {
      const itemStr = localStorage.getItem(key)
      if (!itemStr) {
        return false
      }

      const item: StorageItem = JSON.parse(itemStr)
      
      // 检查是否过期
      if (item.expire && item.timestamp) {
        const now = Date.now()
        const expireTime = item.timestamp + item.expire
        if (now > expireTime) {
          // 已过期，删除并返回 false
          localStorage.removeItem(key)
          return false
        }
      }

      return true
    } catch (error) {
      console.error(`[Storage] 检查键失败: ${key}`, error)
      return false
    }
  }

  /**
   * 清空所有存储（谨慎使用）
   */
  static clear(): void {
    try {
      localStorage.clear()
    } catch (error) {
      console.error('[Storage] 清空存储失败', error)
    }
  }

  /**
   * 获取所有键名
   * 
   * @returns 键名数组
   */
  static keys(): string[] {
    try {
      return Object.keys(localStorage)
    } catch (error) {
      console.error('[Storage] 获取键名列表失败', error)
      return []
    }
  }

  /**
   * 获取存储大小（字节，近似值）
   * 
   * @returns 存储大小
   */
  static size(): number {
    try {
      let total = 0
      for (const key in localStorage) {
        if (localStorage.hasOwnProperty(key)) {
          const item = localStorage.getItem(key)
          if (item) {
            total += item.length + key.length
          }
        }
      }
      return total
    } catch (error) {
      console.error('[Storage] 计算存储大小失败', error)
      return 0
    }
  }
}

/**
 * 便捷方法：直接存储原始值（不包装为 StorageItem）
 * 适用于简单的字符串、数字等值
 */
export class SimpleStorage {
  /**
   * 设置简单值
   */
  static set(key: string, value: string | number | boolean): void {
    try {
      localStorage.setItem(key, String(value))
    } catch (error) {
      console.error(`[SimpleStorage] 设置值失败: ${key}`, error)
      throw error
    }
  }

  /**
   * 获取简单值
   */
  static get(key: string, defaultValue?: string): string | undefined {
    try {
      const value = localStorage.getItem(key)
      return value ?? defaultValue
    } catch (error) {
      console.error(`[SimpleStorage] 获取值失败: ${key}`, error)
      return defaultValue
    }
  }

  /**
   * 获取数字值
   */
  static getNumber(key: string, defaultValue?: number): number | undefined {
    const value = this.get(key)
    if (value === undefined) {
      return defaultValue
    }
    const num = Number(value)
    return isNaN(num) ? defaultValue : num
  }

  /**
   * 获取布尔值
   */
  static getBoolean(key: string, defaultValue?: boolean): boolean | undefined {
    const value = this.get(key)
    if (value === undefined) {
      return defaultValue
    }
    return value === 'true'
  }

  /**
   * 删除值
   */
  static remove(key: string): void {
    try {
      localStorage.removeItem(key)
    } catch (error) {
      console.error(`[SimpleStorage] 删除值失败: ${key}`, error)
    }
  }

  /**
   * 检查键是否存在
   */
  static has(key: string): boolean {
    return localStorage.getItem(key) !== null
  }
}

