import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse, AxiosError } from 'axios'

// 响应数据结构
export interface HttpResponse<T = any> {
  code: number
  errorMessage: string
  data: T
  success?: boolean
}

// 请求配置扩展
export interface RequestConfig extends AxiosRequestConfig {
  // 是否显示成功消息
  showSuccessMessage?: boolean
  // 是否显示错误消息
  showErrorMessage?: boolean
  // 错误消息模式: 'message' | 'modal' | 'none'
  errorMessageMode?: 'message' | 'modal' | 'none'
  // 成功消息模式: 'message' | 'modal' | 'none'
  successMessageMode?: 'message' | 'modal' | 'none'
  // 是否返回原生响应
  isReturnNativeResponse?: boolean
  // 是否转换响应数据
  isTransformResponse?: boolean
}

/**
 * Header 配置函数类型
 * 接收当前配置，返回要添加的 headers
 */
export type HeaderConfigFunction = (config: AxiosRequestConfig) => Record<string, string>

/**
 * 消息处理器接口
 */
export interface MessageHandler {
  success: (message: string) => void
  error: (message: string) => void
  warning: (message: string) => void
  info: (message: string) => void
}

/**
 * 路由处理器接口
 */
export interface RouterHandler {
  push: (path: string) => void
}

/**
 * RequestClient 配置选项
 */
export interface RequestClientOptions extends AxiosRequestConfig {
  /**
   * Header 配置函数
   * 用于动态添加请求头（如 token, namespace 等）
   */
  headerConfig?: HeaderConfigFunction
  
  /**
   * 消息处理器
   * 用于显示成功/错误消息
   */
  messageHandler?: MessageHandler
  
  /**
   * 路由处理器
   * 用于处理登录跳转等
   */
  routerHandler?: RouterHandler
}

/**
 * 默认的 Header 配置函数
 * 从 localStorage 读取 token, namespace, clientId
 */
export const defaultHeaderConfig: HeaderConfigFunction = (_config) => {
  const headers: Record<string, string> = {}
  
  // 从 localStorage 获取 token
  const token = localStorage.getItem('token')
  if (token) {
    headers.Authorization = `Bearer ${token}`
  }
  
  // 从 localStorage 获取 namespace
  const namespace = localStorage.getItem('namespace') || 'default'
  if (namespace) {
    headers['X-Namespace'] = namespace
  }
  
  // 从 localStorage 获取 SSE clientId
  const clientId = localStorage.getItem('gress_sse_client_id')
  if (clientId) {
    headers['X-Client-Id'] = clientId
  }
  
  return headers
}

/**
 * 默认的消息处理器（使用 console）
 */
const defaultMessageHandler: MessageHandler = {
  success: (msg) => console.log('[Success]', msg),
  error: (msg) => console.error('[Error]', msg),
  warning: (msg) => console.warn('[Warning]', msg),
  info: (msg) => console.info('[Info]', msg)
}

/**
 * 默认的路由处理器
 */
const defaultRouterHandler: RouterHandler = {
  push: (path) => {
    if (typeof window !== 'undefined') {
      window.location.href = path
    }
  }
}

class RequestClient {
  private instance: AxiosInstance
  private headerConfig: HeaderConfigFunction
  private messageHandler: MessageHandler
  private routerHandler: RouterHandler

  constructor(options: RequestClientOptions = {}) {
    const { 
      headerConfig = defaultHeaderConfig,
      messageHandler = defaultMessageHandler,
      routerHandler = defaultRouterHandler,
      ...axiosConfig 
    } = options
    
    this.headerConfig = headerConfig
    this.messageHandler = messageHandler
    this.routerHandler = routerHandler
    
    this.instance = axios.create({
      timeout: 30000,
      ...axiosConfig
    })

    this.setupInterceptors()
  }

  /**
   * 设置 Header 配置函数
   */
  setHeaderConfig(headerConfig: HeaderConfigFunction) {
    this.headerConfig = headerConfig
  }

  /**
   * 设置消息处理器
   */
  setMessageHandler(messageHandler: MessageHandler) {
    this.messageHandler = messageHandler
  }

  /**
   * 设置路由处理器
   */
  setRouterHandler(routerHandler: RouterHandler) {
    this.routerHandler = routerHandler
  }

  private setupInterceptors() {
    // 请求拦截器
    this.instance.interceptors.request.use(
      (config: any) => {
        // 使用配置函数动态添加 headers
        if (this.headerConfig) {
          const dynamicHeaders = this.headerConfig(config)
          config.headers = config.headers || {}
          Object.assign(config.headers, dynamicHeaders)
        }

        return config
      },
      (error: AxiosError) => {
        return Promise.reject(error)
      }
    )

    // 响应拦截器
    this.instance.interceptors.response.use(
      (response: AxiosResponse) => {
        return this.handleResponse(response)
      },
      (error: AxiosError) => {
        return this.handleError(error)
      }
    )
  }

  private handleResponse(response: AxiosResponse) {
    const config = response.config as RequestConfig
    const { isReturnNativeResponse, isTransformResponse = true } = config

    // 返回原生响应
    if (isReturnNativeResponse) {
      return response
    }

    // 不转换响应数据
    if (!isTransformResponse) {
      return response.data
    }

    const responseData: HttpResponse = response.data

    if (!responseData) {
      console.error('响应数据为空:', response)
      throw new Error('请求失败，未返回数据')
    }

    // 检查响应数据是否为有效对象
    if (typeof responseData !== 'object') {
      console.error('响应数据类型错误:', typeof responseData, responseData)
      throw new Error('响应数据格式错误')
    }

    const { code, data, errorMessage: msg } = responseData

    // 检查 code 是否存在
    if (code === undefined || code === null) {
      console.error('响应数据缺少 code 字段:', responseData)
      throw new Error('响应数据格式错误：缺少 code 字段')
    }

    // 业务状态码为 200 表示成功
    const hasSuccess = code === 200

    if (hasSuccess) {
      // 显示成功消息
      if (config.successMessageMode === 'message' && msg) {
        this.messageHandler.success(msg)
      }

      return data
    }

    // 处理业务错误
    return this.handleBusinessError(code, msg, config)
  }

  private handleBusinessError(code: number, msg: string, config: RequestConfig) {
    const errorMessageMode = config.errorMessageMode ?? 'message'

    switch (code) {
      case 401: {
        // Token 过期或无效
        localStorage.removeItem('token')
        this.messageHandler.error('登录已过期，请重新登录')
        // 跳转到登录页
        this.routerHandler.push('/login')
        break
      }
      case 403: {
        this.messageHandler.error('没有权限访问该资源')
        break
      }
      case 404: {
        this.messageHandler.error('请求的资源不存在')
        break
      }
      default: {
        if (errorMessageMode === 'message' && msg) {
          this.messageHandler.error(msg)
        }
      }
    }

    throw new Error(msg || '请求失败')
  }

  private handleError(error: AxiosError) {
    const config = error.config as RequestConfig
    const errorMessageMode = config?.errorMessageMode ?? 'message'

    let errorMessage = '请求失败'

    if (error.response) {
      // 服务器返回了错误状态码
      const { status, data } = error.response
      errorMessage = (data as any)?.message || `请求失败 (${status})`
    } else if (error.request) {
      // 请求已发送但没有收到响应
      errorMessage = '网络错误，请检查网络连接'
    } else {
      // 请求配置出错
      errorMessage = error.message || '请求配置错误'
    }

    if (errorMessageMode === 'message') {
      this.messageHandler.error(errorMessage)
    }

    return Promise.reject(error)
  }

  // GET 请求
  get<T = any>(url: string, config?: RequestConfig): Promise<T> {
    return this.instance.get(url, config)
  }

  // POST 请求
  post<T = any>(url: string, data?: any, config?: RequestConfig): Promise<T> {
    return this.instance.post(url, data, config)
  }

  // PUT 请求
  put<T = any>(url: string, data?: any, config?: RequestConfig): Promise<T> {
    return this.instance.put(url, data, config)
  }

  // DELETE 请求
  delete<T = any>(url: string, config?: RequestConfig): Promise<T> {
    return this.instance.delete(url, config)
  }

  // PATCH 请求
  patch<T = any>(url: string, data?: any, config?: RequestConfig): Promise<T> {
    return this.instance.patch(url, data, config)
  }

  // 获取原始 axios 实例
  getAxiosInstance(): AxiosInstance {
    return this.instance
  }
}

/**
 * 创建请求客户端
 * @param options 配置选项
 * @returns RequestClient 实例
 */
export function createRequestClient(options: RequestClientOptions = {}): RequestClient {
  return new RequestClient(options)
}

// 创建默认请求客户端
// 注意：在实际使用中，建议在应用初始化时创建并配置客户端
export const requestClient = createRequestClient({
  baseURL: '/api',
  headers: {
    'Content-Type': 'application/json'
  },
  headerConfig: defaultHeaderConfig
})

// 导出便捷方法
export const request = {
  get: <T = any>(url: string, config?: RequestConfig) =>
    requestClient.get<T>(url, config),

  post: <T = any>(url: string, data?: any, config?: RequestConfig) =>
    requestClient.post<T>(url, data, config),

  put: <T = any>(url: string, data?: any, config?: RequestConfig) =>
    requestClient.put<T>(url, data, config),

  delete: <T = any>(url: string, config?: RequestConfig) =>
    requestClient.delete<T>(url, config),

  patch: <T = any>(url: string, data?: any, config?: RequestConfig) =>
    requestClient.patch<T>(url, data, config)
}

export default request

// 导出 RequestClient 类，供高级用户使用
export { RequestClient }
